import { defineComponent } from "vue";
import {
  LinkableMixin
} from "@inkline/inkline/mixins";
const componentName = "INavItem";
export default defineComponent({
  name: componentName,
  mixins: [
    LinkableMixin
  ],
  inject: {
    nav: {
      default: () => ({
        onItemClick: () => {
        }
      })
    }
  },
  inheritAttrs: false,
  props: {
    active: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    stopPropagation: {
      type: Boolean,
      default: false
    },
    tag: {
      type: String,
      default: "div"
    },
    tabindex: {
      type: [Number, String],
      default: 0
    }
  },
  computed: {
    ariaDisabled() {
      if (this.role === "link") {
        return null;
      }
      return this.disabled ? "true" : "false";
    },
    classes() {
      return {
        "-active": this.active,
        "-disabled": this.disabled
      };
    },
    role() {
      return this.$attrs.to || this.$attrs.href ? "link" : "menuitem";
    },
    tabIndex() {
      return this.disabled ? -1 : this.tabindex;
    }
  },
  methods: {
    onClick(event) {
      if (this.stopPropagation) {
        return;
      }
      this.nav.onItemClick(this, event);
    }
  }
});
